body {
  background-color: #0f1228;
  overflow-x: hidden;
  font-family: Lato, sans-serif;
  color: #e7e9f0;
}

.head-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
}
.head-image {
  width: auto;
  transition: max-width 1s, min-width 1s;
}

.head-image-expanded {
  min-width: 350px;
  max-width: 400px;
}

.head-image-contracted {
  max-width: 60px;
  min-width: 40px;
}

.head-text {
  font-family: 'Hachi Maru Pop', cursive;
  margin: 5px 0 15px 0;
  transition: font-size 1s;
}

.head-text-expanded {
  font-size: 36px;
}

.head-text-contracted {
  font-size: 12px;
}

.result-name-card {
  color: #e7e9f0;
  background-color: #282d4f;
  margin: 10px 15px;
  min-width: 100px;
  text-align: center;
  padding: 10px 20px;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.1);
  transition: all 0.35s;
}

.card-link {
  text-decoration: none;
}

.result-name-card:hover {
  background-color: #4a5079;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.result-name {
  margin: 0;
  font-weight: 300;
}

.results-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 20px 40px;
}

.search-container {
  display: flex;
  justify-content: center;
  margin: 10px;
}

.search-input {
  padding: 8px 15px;
  font-size: 15px;
  width: 250px;
  border-width: 0;
  background-color: #a592f2;
  color: #e7e9f0;
  border-radius: 50px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.search-input:focus {
  outline: none;
}

